.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App button {
  width: 150px;
  border: 1px solid #03a9f4;
  background-color: #03A9F4;
  margin-top: 20px;
  padding: 10px 40px;
  font-size: 15px;
  border-width: 0px;
  border-radius: 20px;
  color: white;
  font-weight: 700;
  box-shadow: 0 10px 17px rgb(80 169 245 / 50%);
}

.App button:disabled {
  border: 1px solid #e7e7e7;
  background-color: #e7e7e7;
  box-shadow: none;

}

.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  border: 1px solid #eee;
  border-radius: 20px;
  padding: 20px;
}

.input-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.option-container span {
  width: 40%;
  display: inline-block;
  margin-right: 10px;
  justify-content: flex-start;
  text-align: right;
}

.option-container input  {
  border: 1px solid #e7e7e7;
}

.option-container select {
  width: 50%;
  border: 1px solid #e7e7e7;
}

.calculated-container {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

.calculated-container h3 {
  align-self: flex-start;
}

.result-container {
  display: flex;
  flex-direction: column;
  align-items: center; 
  width: 100%;
  border: 1px solid #e7e7e7; 
}

.copy-button {
  position: absolute;
  background-color: #e7e7e7;
  width: 60px;
  height: 20px;
  border-radius: 500px;
  border:  1px solid #e7e7e7;
  margin-left: 10px;
  font-weight: 500;
  
}

.table-header {
  background-color: #03a9f4;
}

.table-header th{
  color: #eee;
  font-size: 15px;
  font-weight: 800;
}